import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue'	
export default {
	components: {
		uniLoadMore
	},
    data() {
        return {
			objectType: ['全部', '消费', '退款', '充值', '提现', '佣金', '平台调整'],
			index: 0,	// 默认选中的类型	索引
			page: 1,
			limit: 10,
			list: [],
			states: [0, 1, 2, 3, 4, 5, 7], // 不同类型的状态
			loadStatus: 'more'
        }
    },
	onLoad (e) {
		if(e.status){
			this.index = this.states.indexOf(parseInt(e.status));
		}else{
			this.balances()//修复多次加载问题
		}
	},
	onReachBottom () {
		if (this.loadStatus === 'more') {
			this.balances()
		}
	},
    methods: {
		// 切换类型
		changeState (e) {
			if (this.index !== e.target.value) {
				this.index = e.target.value;
				this.page = 1
				this.list = []
			}
		},
		// 获取余额明细
		balances () {
			let data = {
				type: this.states[this.index],
				page: this.page,
				limit: this.limit
			}
			
			this.loadStatus = 'loading'
			
			this.$api.getBalanceList(data, res => {
				if (res.status) {
					if (this.page >= res.total) {
						// 没有数据了
						this.loadStatus = 'noMore'
					} else {
						// 未加载完毕
						this.loadStatus = 'more'
						this.page ++
					}
					this.list = [...this.list, ...res.data]
				} else {
					this.$common.errorToShow(res.msg)
				}
			})
		}
    },
	watch: {
		index () {
			this.balances();
		}
	}
}
